import { EmojiText } from "../../../UI/emojiText/emojiText";
import cls from "./animation.module.scss";
export function Animation() {
  const blocks = [
    {
      text: "Wake up early",
      src: "test-project/images/animation/1.png",
      style: { background: "#13C962", animationDelay: "0s" },
    },
    {
      text: "Learn something new",
      src: "test-project/images/animation/2.png",
      style: { background: "#13C962", animationDelay: "1s" },
    },
    {
      text: "Learn an instrument",
      src: "test-project/images/animation/3.png",
      style: { background: "#13C962", animationDelay: "2.5s" },
    },
    {
      text: "Pray",
      src: "test-project/images/animation/4.png",
      style: { background: "#13C962", animationDelay: "5s" },
    },
    {
      text: "Journal",
      src: "test-project/images/animation/5.png",
      style: { background: "#13C962", animationDelay: "3s" },
    },
    {
      text: "Learn a language",
      src: "test-project/images/animation/6.png",
      style: { background: "#13C962", animationDelay: "8.2s" },
    },
    {
      text: "Read",
      src: "test-project/images/animation/7.png",
      style: { background: "#13C962", animationDelay: "-1s" },
    },
    {
      text: "Educational podcast",
      src: "test-project/images/animation/8.png",
      style: { background: "#13C962", animationDelay: "-4s" },
    },
    {
      text: "Meditate",
      src: "test-project/images/animation/9.png",
      style: { background: "#13C962", animationDelay: "-3.8s" },
    },
    {
      text: "Littering ",
      src: "test-project/images/animation/10.png",
      style: { background: "#FF6E41", animationDelay: "-8s" },
    },
    {
      text: "Twitter",
      src: "test-project/images/animation/11.png",
      style: { background: "#FF6E41", animationDelay: "-4.2s" },
    },
    {
      text: "Nail Biting",
      src: "test-project/images/animation/12.png",
      style: { background: "#FF6E41", animationDelay: "-0.5s" },
    },
    {
      text: "Gambling",
      src: "test-project/images/animation/13.png",
      style: { background: "#FF6E41", animationDelay: "-15s" },
    },
    {
      text: "Video Games",
      src: "test-project/images/animation/14.png",
      style: { background: "#FF6E41", animationDelay: "-5s" },
    },
    {
      text: "Smoking",
      src: "test-project/images/animation/15.png",
      style: { background: "#FF6E41", animationDelay: "-17.5s" },
    },
    {
      text: "Junk food",
      src: "test-project/images/animation/16.png",
      style: { background: "#FF6E41", animationDelay: "-14s" },
    },
    {
      text: "Caffeine",
      src: "test-project/images/animation/17.png",
      style: { background: "#FF6E41", animationDelay: "-17s" },
    },
    {
      text: "Excessive Drinking",
      src: "test-project/images/animation/18.png",
      style: { background: "#FF6E41", animationDelay: "-19s" },
    },
    {
      text: "Being in Bad Relationships",
      src: "test-project/images/animation/19.png",
      style: { background: "#FF6E41", animationDelay: "-16.4s" },
    },
    {
      text: "Focusing on the Negatives",
      src: "test-project/images/animation/20.png",
      style: { background: "#FF6E41", animationDelay: "-13s" },
    },
  ];

  const jsxBlocks = blocks.map((block, i) => {
    const animationDelayModifier = (i + Math.floor(i / 5)) % 5;
    console.log(animationDelayModifier);
    return (
      <div
        className={cls.animationBlock}
        style={{
          ...block.style,
          bottom: `${((i + 1) % 5) * 15 + 15}%`,
          animationDelay: `${
            (((animationDelayModifier + 1) % 5) * 30 + 15) / 10 + Math.random()
          }s`,
        }}
      >
        <div className={cls.emojiText}>
          <img src={block.src} className={cls.emoji} alt="emoji"></img>
          <div className={cls.blockText}>{block.text}</div>
        </div>
      </div>
    );
  });
  return (
    <div className={cls.wrapper}>
      <div className={cls.title}>We are what we do every day</div>
      <div className={cls.text}>
        Habits bring you system to change, either good or bad
      </div>
      <div className={cls.animation}>{jsxBlocks}</div>
    </div>
  );
}
